import Cookies from "js-cookie";
import { useContext, useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory, useLocation } from "react-router-dom";
import { AdminContext } from "context/AdminContext";
import AdminServices from "services/AdminServices";
import { notifyError, notifySuccess } from "utils/toast";

const useLoginSubmit = () => {
  const [loading, setLoading] = useState(false);
  const { dispatch } = useContext(AdminContext);
  const history = useHistory();
  const location = useLocation();
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const onSubmit = ({ name, email, verifyEmail, password, role }) => {
    setLoading(true);
    const cookieTimeOut = 0.5;
    if (location.pathname === "/login") {
      AdminServices.loginAdmin({ email, password })
        .then((res) => {
          const designation = res.data?.admin_data?.designation;
          console.log(res.data, "res");
          if (designation === "waiter" || designation === "housekeeper") {
            throw new Error("login denied.");
          } else if (res.status === true) {
            console.log("true", "res");
            setLoading(false);
            localStorage.setItem("token", res.data.access_token);

            localStorage.setItem("designation", designation);
            let ress = {
              token: res.data.access_token,
              _id: res.data.admin_data.id,
              phone: res.data.admin_data.mobile,
              email: res.data.admin_data.email,
              image:
                "https://res.cloudinary.com/ahossain/image/upload/v1686725022/customer/download%281%29.jpg",
            };
            notifySuccess("Login Success!");
            dispatch({ type: "USER_LOGIN", payload: ress });
            Cookies.set("adminInfo", JSON.stringify(ress), {
              expires: cookieTimeOut,
            });
            history.replace("/dashboard");
          }
        })
        .catch((err) => {
          notifyError(err ? err.response.data.message : err.message);
          setLoading(false);
        });
    }

    // if (location.pathname === '/signup') {
    //   AdminServices.registerAdmin({ name, email, password, role })
    //     .then((res) => {
    //       if (res) {
    //         setLoading(false);
    //         notifySuccess('Register Success!');
    //         dispatch({ type: 'USER_LOGIN', payload: res });
    //         Cookies.set('adminInfo', JSON.stringify(res), {
    //           expires: cookieTimeOut,
    //         });
    //         history.replace('/');
    //       }
    //     })
    //     .catch((err) => {
    //       notifyError(err ? err.response.data.message : err.message);
    //       setLoading(false);
    //     });
    // }
    let ress = {
      // "token": res.data.access_token,
      // "_id": res.data.admin_data.id,
      // "phone": res.data.admin_data.mobile,
      // "email": res.data.admin_data.email,
      image:
        "https://res.cloudinary.com/ahossain/image/upload/v1686725022/customer/download%281%29.jpg",
    };
    // if (location.pathname === '/forgot-password') {
    //   AdminServices.forgetPassword({ verifyEmail })
    //     .then((res) => {
    //       setLoading(false);
    //       notifySuccess(res.message);
    //     })
    //     .catch((err) => {
    //       setLoading(false);
    //       notifyError(err ? err.response.data.message : err.message);
    //     });
    // }
  };
  return {
    onSubmit,
    register,
    handleSubmit,
    errors,
    loading,
  };
};

export default useLoginSubmit;
